import { BaseDatasourceOptions } from '../../base-datasource.options';
import { PostgresConnectionCredentialsOptions } from './postgres-connection-credentials.options';

export interface PostgresConnectionOptions
  extends BaseDatasourceOptions,
    PostgresConnectionCredentialsOptions {
  readonly type: 'postgres';

  /**
   * sets the application_name var to help db administrators identify
   * the service using this connection. Defaults to 'undefined'
   */
  readonly applicationName?: string;

  readonly connectTimeoutMS?: number;

  /**
   * Include notification messages from Postgres server in client logs
   */
  readonly logNotifications?: boolean;

  readonly disableParsing?: boolean;
}
